import {
  makeStyles,
  Container,
  Grid,
  Typography,
  useTheme,
} from "@material-ui/core";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";

import FeaturedProjects from "./FeaturedProjects";
import InTheNews from "./InTheNews";

const Home = () => {
  const classes = useStyles();
  const theme = useTheme();
  return (
    <Container className={classes.root}>
      <Grid container spacing={4}>
        <Grid item xs={12} md={6}>
          <Typography component="h1" variant="h3" className={classes.quote}>
            Exploring Data Science and Machine Learning
          </Typography>
          <Typography
            component="h2"
            variant="h5"
            className={classes.quoteSubtitle}
          >
            one dataset at a time...
          </Typography>

          <Typography component="h2" variant="h4" className={classes.explore}>
            Explore my portfolio below
            <div style={{ marginTop: theme.spacing(2) }}>
              <KeyboardArrowDownIcon fontSize="large" />
            </div>
          </Typography>
        </Grid>
        <Grid item xs={12} md={6} className={classes.imgContainer}>
          <img
            src={process.env.PUBLIC_URL + "/me2.jpg"}
            className={classes.img}
            alt="Gregory Glatzer"
          />
        </Grid>
      </Grid>

      <div id="portfolio" className={classes.portfolio}>
        <FeaturedProjects/>
        <InTheNews/>
      </div>
    </Container>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    width: "80%",
    marginBottom: theme.spacing(5),
    marginTop: theme.spacing(5),
  },
  imgContainer: {
    justifyContent: "center",
  },
  img: {
    width: "100%",
    height: "auto",
  },
  quote: {
    fontWeight: "bold",
    color: theme.palette.primary.main,
  },
  quoteSubtitle: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(3),
    fontWeight: "bold",
    color: "darkgrey",
  },
  explore: {
    textAlign: "center",
    color: "grey",
    marginTop: theme.spacing(9),
  },
  portfolio: {
    marginTop: theme.spacing(5),
  },
}));

export default Home;
